@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@keyframes float1 {
  0% { transform: translateY(0px) translateX(0px) rotate(0deg); }
  50% { transform: translateY(-20px) translateX(10px) rotate(5deg); }
  100% { transform: translateY(0px) translateX(0px) rotate(0deg); }
}

@keyframes float2 {
  0% { transform: translateY(0px) translateX(0px) rotate(0deg); }
  50% { transform: translateY(15px) translateX(-10px) rotate(-5deg); }
  100% { transform: translateY(0px) translateX(0px) rotate(0deg); }
}

@keyframes float3 {
  0% { transform: translateY(0px) translateX(0px) rotate(0deg); }
  50% { transform: translateY(-10px) translateX(15px) rotate(3deg); }
  100% { transform: translateY(0px) translateX(0px) rotate(0deg); }
}

.animate-float1 { animation: float1 6s infinite ease-in-out; }
.animate-float2 { animation: float2 5s infinite ease-in-out; }
@keyframes blob {
  0% {
    transform: translate(0px, 0px) scale(1);
  }
  33% {
    transform: translate(30px, -50px) scale(1.1);
  }
  66% {
    transform: translate(-20px, 20px) scale(0.9);
  }
  100% {
    transform: translate(0px, 0px) scale(1);
  }
}

.animate-blob {
  animation: blob 7s infinite;
}

.animation-delay-2000 {
  animation-delay: 2s;
}

.animation-delay-4000 {
  animation-delay: 4s;
}

